<template>
  <div>
    <content-title
      title="关键字管理"
      tips="关键字涉及邮件积分筛选过滤，请谨慎设置。关键字导入请使用关键字导出的表格格式，和文件格式（.xls）">
    </content-title>
    <nav-bar :nav="posList" @navBack="navBack"></nav-bar>
    <content-c>
      <div class="add-keywords">
        <div class="add" @click="addKeywordsDialogVisible = true">+关键字</div>
        <div class="import">导入
          <!--<form id="fileForm" enctype="multipart/form-data" method="post" action="/oa/resume/key/import">-->
            <input type="file" accept="application/vnd.ms-excel" name="xls" class="import-file"
                   @change="importFile($event)">
          <!--</form>-->
        </div>
        <div class="export" @click="exportFile">导出</div>
      </div>
      <ul class="dep-box">
        <li v-for="key in keywordsList">
          <span>{{key.integral}}分</span>&nbsp;&nbsp;-&nbsp;&nbsp;<span>{{key.value}}</span><i
          class="sprites icon-del" @click="delKeywords(key)"></i><i
          class="sprites icon-edit" @click="editKeywords(key)"></i></li>
      </ul>

      <div class="com-page-box">
        <div class="com-pages">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="keywordsListTotal"
            v-if="keywordsListTotal > 10">
          </el-pagination>
        </div>
      </div>
    </content-c>

    <!--新增关键字-->
    <com-dialog :show="addKeywordsDialogVisible"
                title="标题"
                :toastShowStyle="{borderRadius: '10px', width: '40%'}">
      <el-form :model="formData"
               :rules="rules"
               ref="formRef1"
               label-width="100px"
               class="demo-ruleForm"
               label-position="top"
      >

        <el-form-item label="分值(可为负)" prop="integral">
          <el-input-number style="width: 40%" v-model="formData.integral" controls-position="right" :min="-100"
                           :max="100"></el-input-number>
        </el-form-item>

        <el-form-item label="关键字" prop="value">
          <el-input v-model="formData.value"></el-input>
        </el-form-item>
      </el-form>
      <div slot="title" class="com-dialog-title t-1">
        新增关键词
        <!--<div class="close x-color-1" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #64A5E7; border-color: #64A5E7;" type="primary"
                   @click="handleAddKeywordsSave('formRef1')">保存
        </el-button>
      </div>
    </com-dialog>

    <!--编辑关键字-->
    <com-dialog :show="editKeywordsDialogVisible"
                title="标题"
                :toastShowStyle="{borderRadius: '10px', width: '40%'}">
      <el-form :model="formData"
               :rules="rules"
               ref="formRef2"
               label-width="100px"
               class="demo-ruleForm"
               label-position="top"
      >

        <el-form-item label="分值(可为负)" prop="integral">
          <el-input-number style="width: 40%" v-model="formData.integral" controls-position="right" :min="-100"
                           :max="100"></el-input-number>
        </el-form-item>

        <el-form-item label="关键字" prop="value">
          <el-input v-model="formData.value"></el-input>
        </el-form-item>
      </el-form>
      <div slot="title" class="com-dialog-title t-2">
        编辑关键词
        <!--<div class="close x-color-2" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #000; border-color: #000;" type="primary"
                   @click="handleAddKeywordsSave('formRef2')">保存
        </el-button>
      </div>
    </com-dialog>
  </div>
</template>
<script>
  import contentTitle from '../../componentsBody/contentTitle.vue'
  import contentC from '../../componentsBody/contentC.vue'
  import navBar from '../../componentsBody/navBarKeywords.vue'
  import comDialog from '../../../components/toast/dialog.vue'
  import { mapState, mapActions } from 'vuex'
  import { serverIp } from '../../../utils/baseConfig'

  export default {
    name: '',
    props: {},
    data () {
      return {
        addKeywordsDialogVisible: false,
        formData: {
          integral: 0,
          value: ''
        },
        keywordsExcelFile: '',
        rules: {
//          integral: [
//            {required: true, message: '请输入分值'},
//            {min: -100, max: 100, message: '分值在-100到100之间'}
//          ],
          value: [
            {required: true, message: '请输入关键字', trigger: 'blur'},
            {min: 1, message: '关键字最短长度为1', trigger: 'blur'}
          ]
        },
        currentPos: {},
        editKeywordsDialogVisible: false,
        currentPage: 1, // 当前页
        pageSize: 20, // 每页条数
        pageSizes: [10, 20, 30],
      }
    },
    computed: {
      ...mapState('department', [
        'departmentList'
      ]),
      ...mapState('position', [
        'posList'
      ]),
      ...mapState('keywords', [
        'keywordsList',
        'keywordsListTotal',
        'addKeywordsStatus',
        'delKeywordsStatus',
        'importStatus',
      ]),
    },
    watch: {
      addKeywordsStatus (d) {
        if (d.status) {
          this.closeDialog()
          this.getKeywordsData(this.currentPos)
        }
        if (d.statusMsg) {
          this.$message({
            type: 'success',
            message: d.statusMsg
          })
        }
      },
      delKeywordsStatus (d) {
        if (d.status) {
          this.closeDialog()
          this.getKeywordsData(this.currentPos)
        }
        if (d.statusMsg) {
          this.$message({
            type: 'success',
            message: d.statusMsg
          })
        }
      },
      importStatus (d) {
        if (d.status === 1) {
          this.getKeywordsData(this.currentPos)
          this.$message({
            type: 'success',
            message: d.msg
          })
        } else if (d.status === 2) {
          this.$message({
            type: 'error',
            message: d.msg
          })
        }
      }
    },
    methods: {
      ...mapActions('department', [
        'ac_departmentList'
      ]),
      ...mapActions('position', [
        'ac_posList'
      ]),
      ...mapActions('keywords', [
        'ac_keywordsList',
        'ac_addAndModifyKeywords',
        'ac_delKeywords',
        'ac_keywordsExport',
        'ac_keywordsImport',
      ]),
      navBack (obj) {
        this.currentPos = obj.nav
        this.getKeywordsData(obj.nav)
      },
      getKeywordsData (obj) {
        if (obj.id !== -1) {
          this.ac_keywordsList({
            entity: {job: {id: obj.id}},
            pageNo: this.currentPage - 1,
            pageSize: this.pageSize
          })
        } else {
          this.ac_keywordsList({
            pageNo: this.currentPage - 1,
            pageSize: this.pageSize
          })
        }
      },
      handleAddKeywordsSave (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let param = {
              integral: this.formData.integral,
              value: this.formData.value
            }
            if (this.currentPos.id !== -1) { // 区分部门关键字和通用关键字
              param.job = {
                id: this.currentPos.id
              }
            }
            if (this.formData.id) { // 区分添加和修改
              param.id = this.formData.id
            }
            this.ac_addAndModifyKeywords(param)
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      closeDialog () {
        this.addKeywordsDialogVisible = false
        this.editKeywordsDialogVisible = false
        this.formData = {
          integral: 0,
          value: ''
        }
      },
      editKeywords (key) {
        this.formData = {
          id: key.id,
          integral: key.integral,
          value: key.value
        }
        this.editKeywordsDialogVisible = true
      },
      delKeywords (key) {
        this.$confirm('确定删除该关键字, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.ac_delKeywords({
            resumeKeyId: key.id
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      },
      handleSizeChange (size) {
        this.pageSize = size
        this.getKeywordsData(this.currentPos)
      },
      handleCurrentChange (page) {
        this.currentPage = page
        this.getKeywordsData(this.currentPos)
      },
      importFile (e) {
        let files = e.target.files || e.dataTransfer.files
//         实例化一个表单数据对象
        let formData = new FormData()
        formData.append('filename', files[0].name)
        formData.append('xls', files[0])
        this.ac_keywordsImport(formData)
      },
      exportFile () {
        if (this.currentPos.id !== -1) {
          window.location.href = serverIp + '/oa/resume/key/' + this.currentPos.id + '/export'
        } else {
          window.location.href = serverIp + '/oa/resume/key/0/export'
        }
      }
    },
    components: {
      contentTitle,
      contentC,
      navBar,
      comDialog,
    },
    beforeCreate () {},
    created () {
      this.ac_posList({
        pageNo: 0,
        pageSize: 10000
      })
      this.ac_keywordsList({
        pageNo: this.currentPage - 1,
        pageSize: this.pageSize
      })
      this.currentPos = {name: '通用', id: -1}
    },
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../styles/sprites/sprites.css";
  @import "../../../styles/common";

  $li-w: 400px;
  $add-w: 200px;

  .add-keywords {
    width: $li-w;
    display: flex;
    font-size: 12px;
    margin-left: 20px;
    .add {
      color: #fff;
      border-radius: 3px;
      width: $add-w;
      margin-right: 10px;
      background-color: #64A5E7;
      padding: 3px 0;
      text-align: center;
      cursor: pointer;
    }
    .import {
      color: #fff;
      border-radius: 3px;
      width: ($li-w - $add-w -10px)/2;
      margin-right: 10px;
      background-color: #FEC71E;
      padding: 3px 0;
      text-align: center;
      cursor: pointer;
      position: relative;
      overflow: hidden;
      .import-file {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        opacity: 0;
      }
    }
    .export {
      color: #fff;
      border-radius: 3px;
      width: ($li-w - $add-w -10px)/2;
      background-color: #5AD1D1;
      padding: 3px 0;
      text-align: center;
      cursor: pointer;
    }
  }

  .dep-box {
    display: flex;
    flex-wrap: wrap;
    li {
      width: $li-w;
      padding: 5px 10px;
      border: 1px solid #E1E1E1;
      background-color: #EFEFEF;
      font-size: 12px;
      margin: 14px 20px;
      border-radius: 5px;
      color: #42454A;
      box-sizing: border-box;
      i {
        float: right;
        margin-left: 20px;
        cursor: pointer;
      }
      &:after {
        content: '';
        display: inline-block;
        clear: both;
      }
    }
  }

  .t-1 {
    background-color: #64A5E7;
  }

  .x-color-1 {
    color: #64A5E7;
  }

  .t-2 {
    background-color: #000000;
  }

  .x-color-2 {
    color: #000000;
  }
</style>



